<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">Symbol Table</h3>
      <div class="card-tools">
        <div class="input-group">
          <input type="text" class="form-control form-control-lg" placeholder="Search by Symbol" v-model="search" @keyup="filtered(true)">
          <div class="input-group-append">
            <button type="submit" class="btn btn-lg btn-default">
              <i class="fa fa-search"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="card-body">
      <div v-if="data.length">
        <table class="table table-bordered">
          <thead>
          <tr>
            <th>Enable</th>
            <th>Symbol</th>
            <th v-for="(exchange,i) in data[0].exchanges" :key="i">{{exchange.title}}</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(row,j) in visible" :key="j" @click="operation(j)" style="cursor: pointer">
            <td>
              <input type="checkbox" :checked="row.enabled">
            </td>
            <td>{{row.label}}</td>
            <td v-for="(ex,l) in row.exchanges" :key="l" :class="ex.enabled ? 'text-success' : 'text-danger'">{{ex.enabled ? 'Yes' : 'No'}}</td>
          </tr>
          </tbody>
        </table>
        <div class="card-tools" v-if="paginator.pages > 1">
          <ul class="pagination pagination-lg">
            <li class="page-item" @click="prevPage"><a href="#" class="page-link">Â«</a></li>
            <li class="page-item" @click="setPage(1)"><a href="#" class="page-link" :class="paginator.curPage === 1 ? 'bg-primary' : ''">1</a></li>
            <li class="page-item"><a href="#" class="page-link" :class="paginator.curPage > 1 && paginator.curPage < paginator.pages ? 'bg-primary' : ''">
              {{paginator.curPage > 1 && paginator.pages > paginator.curPage ? paginator.curPage : '...'}}
            </a></li>
            <li class="page-item" @click="setPage(paginator.pages)"><a href="#" class="page-link" :class="paginator.curPage === paginator.pages ? 'bg-primary' : ''">{{paginator.pages}}</a></li>
            <li class="page-item" @click="nextPage"><a href="#" class="page-link">Â»</a></li>
          </ul>
        </div>
      </div>
      <div v-else class="loader">
        <div class="p-5 text-center text-black-50">
          <i class="spinner-border"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'HelloWorld',
  data: function (){
    return {
      data:[],
      visible:[],
        filter:[],
      search:'',
      url:'',
      token:'',
      paginator:{
        curPage:1,
        pages:1
      }
    }
  },
  mounted() {
    this.url = document.getElementById('app').getAttribute('data-url')
    this.token = document.getElementById('app').getAttribute('data-token')

    axios(this.url + '/list', {
      headers: {Authorization: 'Bearer ' + this.token}
    }).then(r => {
      for (let key in r.data.data) {
        this.data.push(r.data.data[key])
      }
      this.filtered()
      this.setPaginator()
    })
  },
  methods:{
    filtered:function (refreshPaginator) {
      this.filter = this.data.filter((symbol) => {
        return symbol.label !== undefined && symbol.label.includes(this.search)
      })
      this.visible = this.filter.slice(50 * (this.paginator.curPage - 1),50 * this.paginator.curPage)

      if (refreshPaginator)
        this.setPaginator()
    },
    setPaginator:function () {
      this.paginator = {
        curPage: 1,
        pages: Math.floor(this.filter.length / 50) + 1
      }
    },
    nextPage:function () {
      if (this.paginator.curPage < this.paginator.pages)
      this.paginator.curPage ++

      this.filtered()
    },
    prevPage:function () {
      if (this.paginator.curPage > 1)
      this.paginator.curPage --

      this.filtered()
    },
    setPage:function (number) {
      this.paginator.curPage = number

      this.filtered()
    },
    operation:function (i) {
      let volume = 0

      if (!this.visible[i].enabled)
        volume = window.prompt('Set volume in '+this.visible[i].label.split(':')[1])

      axios.post(this.url+(this.visible[i].enabled ? '/delete' : '/add'),{symbol:this.visible[i].label,volume},{
        headers:{Authorization:'Bearer '+this.token}
      })
      this.data[i].enabled = !this.visible[i].enabled
      this.filtered()
    }
  }
}
</script>